            <router-link to="/home" class="back">
                <i class="fas fa-chevron-left"></i>
                Back
            </router-link>
            <router-link to="/edit-profile" class="edit">
                <i class="fas fa-pen"></i>
                Edit
            </router-link>
            <router-link v-if="auth.type === `admin`" :to="{ name: 'AdminDashboard' }" class="edit">
