                        <span class="batch">Batch: {{ auth.batch }}</span>
                    </div>
                    <div class="user-card-image"
                         :style="auth.image ? { 'background-image': 'url(' + $server + auth.image + ')' } : {
                         'background-image': 'url(' + $profile + ')' }"></div>
                    <div class="user-card-content">
                        <h3 class="name">
                            {{ auth.fullnameEng ? auth.fullnameEng : '...' }} <br>
                            <span>({{ auth.nickname ? auth.nickname : '...' }})</span>
                        </h3>
                        <div v-if="!auth.university">...</div>
                        <div v-else>
                          {{ auth.university }}
                        </div>
                        <span>{{ auth.qt1 }}</span>
                        <div>
                            <router-link :to="{ name: 'ResetPassword' }"> reset password </router-link>
                        </div>
                    </div>
                </div>
            </div>
            <div class="column">
                <div class="box">
