                        <span>{{ auth.phone ? auth.phone : '...' }}</span>
                    </div>
                    <div class="box-item">
                        <span class="label">Whatsapp Number</span>
                        <span>{{ auth.whatsapp ? auth.whatsapp : '...' }}</span>
                    </div>
