</template>

<script>
    import appLoading from '../components/Loading'
    import {mapGetters} from 'vuex'

    export default {
        components: {
          appLoading
        },
        data: () => ({
           isLoading: false,
            schools:[
                {name:"Kasetsart University", id:"s1"},
                {name:"Thammasart University", id:"s2"},
                {name:"Chulalongkorn University", id:"s3"},
                {name:"Chiangmai University", id:"s4"},
                {name:"KhonKaen University", id:"s5"},
                {name:"Songkranakarin University", id:"s6"},
                {name:"Other", id:"s7"}
            ],
            batchs:[
                {batch:"Batch 1 (1992)", id:"b1"},
                {batch:"Batch 2 (1993)", id:"b2"},
                {batch:"Batch 3 (1994)", id:"b3"},
                {batch:"Batch 4 (1995)", id:"b4"},
                {batch:"Batch 5 (1996)", id:"b5"},
                {batch:"Batch 6 (1997)", id:"b6"}
            ],
        }),
        computed: {
          ...mapGetters([
              'auth'
          ]),
            university () {
              return this.schools.filter(school=>{
                  return school.id === this.auth.university
              })
            },
            batch () {
              return this.batchs.filter(batch => {
                  return batch.id === this.auth.batch
              })
            }
        },
        methods: {
           async logout () {
              try {
                  this.isLoading = true
                  await this.$store.dispatch('logout')
                  this.isLoading = false
                  this.$notify({
                      group: 'foo',
                      title: 'ອອກຈາກລະບົບແລ້ວ',
                  });
                  this.$router.replace({ name: 'login' })
              } catch (e) {
                  this.isLoading = false
                  throw new Error(e)
              }
            }
        }
    }
</script>

